  text-align: left;
  border-radius: 1rem;
  cursor: pointer;
}
.main__aside .btn__actions i {
  margin-right: 1rem;
  font-size: 2rem;
}

.aside__calender ol {
  list-style: none;
  padding: 0;
}
.aside__calender .calender__title {
  background-color: #40C2D6;
  color: #FFFFFF;
  text-transform: uppercase;
  padding: 0 3rem;
}
.aside__calender .calender__items {
  margin: 0;
  padding: 0;
